import React from "react";
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

import Layout from "../components/layouts/layout"
import Container from "../components/container"
import Section from "../components/section"
import Link from "../components/Link"
import Seo from "../components/seo"

const IndexPage = ({data}) => {
  const { allMdx } = data // data.mdx holds your post data
  const posts = allMdx.edges
  return (
    <Layout title={"Home"}>
    <Section>
      <Container>

      <div className="column is-8 is-offset-2 content">
        <h2>News</h2>

        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.frontmatter.slug
          const artDate = node.frontmatter.date
          let image = null
          let alt = null
          if (node.frontmatter.images) {
            image = node.frontmatter.images[0]
            alt = image.caption | image.name | title
          }

          return (
            <div key={node.frontmatter.slug} className="box">
              <div className="box-header">
                <h4 className="title is-4">{title}
                  <span className="labels is-pulled-right">
                    <a href="label.html"> </a>
                  </span>
                </h4>
                <small>{artDate}</small>
              </div>
              <div className="box-content">
                <div className="columns">
                  {(image) && (
                    <div className="column is-2">
                      <GatsbyImage
                        image={getImage(image.url)}
                        alt={alt}
                      />
                    </div>
                  )}
                  <div className="column">
                    <div className="box-content-text">
                      <p>{node.excerpt}</p>
                    </div>
                    <div className="has-text-right">
                      <Link to={node.frontmatter.slug} className="button is-small">Lesen</Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
        })}

      </div>

      </Container>
    </Section>
    </Layout>
  )
}

export const Head = () => <Seo title="TV Zofingen Handball" />

export default IndexPage

export const frontNewsQuery = graphql`
  query frontNewsQuery {
    allMdx(
      filter: {frontmatter: {slug: {regex: "/news/"}}}
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 3
    ) {
      edges {
        node {
          excerpt(pruneLength: 350)
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            slug
            title
            images {
              caption
              name
              url {
                childImageSharp {
                  gatsbyImageData(
                    layout: CONSTRAINED
                    width: 256
                    height: 256
                    placeholder: BLURRED
                    transformOptions: {fit:COVER, cropFocus:ATTENTION}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
              }
            }
          }
        }
      }
    }
  }
`
